import { getFeed } from "../../data/api";
import useApiData from "../../data/useApiData";

export default function useFeed(topic: string) {
  const { data, ...others } = useApiData(
    () => getFeed(topic),
    [],
    [topic /*    */],
  );

  return {
    feed: data.sort((a, b) => b.timestamp - a.timestamp),
    ...others,
  };
}
